import React from "react";

export const TodoItems = ({ todo, onDelete }) => {
  // const date = new Date().toString();
  return (
    
    <>
    <div className="py-2">
      <ul>
    <li>

      <h4>{todo.title}</h4>
      <p>{todo.desc}</p>
      {/* <p>{date}</p> */}
      <button className="btn-sm btn-danger btn " onClick={() => onDelete(todo)}>
        Delete
      </button>
    </li>
      </ul>
    </div>
    <hr />
    </>
  );
};
